.containers {
    background: #091115;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px 50px;
}

.heading{
    padding: 50px;
    background: #091115;
    text-align: center;
    color: #EAF3F9;
}
.heading h1{
    color: #EAF3F9;
    font-size: 65px;
    font-weight: 700;
}

.cards {
  position: relative;
  min-width: 300px;
  height: 300px;
  box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2),
    inset -5px -5px 15px rgba(255, 255, 255, 0.1),
    5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  margin: 30px;
  transition: 0.5s;
}

.containers .cards:nth-child(1) .box .content a {
  background: #2196f3;
}

.containers .cards:nth-child(2) .box .content a {
  background: #e91e63;
}

.containers .cards:nth-child(3) .box .content a {
  background: #23c186;
}
.containers .cards:nth-child(4) .box .content a {
  background: #2196f3;
}
.containers .cards:nth-child(5) .box .content a {
  background: #e91e63;
}
.containers .cards:nth-child(6) .box .content a {
  background: #23c186;
}

.box {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  background: linear-gradient(232.68deg, #2DD1DB -8.55%, #2D9CDB 57.36%, #1A8ED0 100%);
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: 0.5s;
}

.box:hover {
  transform: translateY(-50px);
}

.box:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  /* background: rgba(255, 255, 255, 0.03); */
  /* background: rgb(45, 209, 219, 0.13); */
}

.content {
  padding: 20px;
  text-align: center;
}

.content h2 {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.4);
}

.content p {
  font-size: 1rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  transition: 0.5s;
}

.content a {
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  background: black;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
.content a:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: #fff;
  color: #000;
}
